const axios = require('axios');

// Example Shiprocket API credentials
const API_KEY = "your_api_key";
const API_SECRET = "your_api_secret";

// API base URL
const API_URL = "https://apiv2.shiprocket.in/v1/external/";

// Function to create a shipment
async function createShipment(orderDetails) {
  try {
    // Shiprocket authentication
    const authResponse = await axios.post(`${API_URL}auth/login`, {
      email: "your_email@example.com",
      password: "your_password"
    });
    
    const token = authResponse.data.token; // Token for authentication
    
    // Prepare shipment details for Shiprocket API
    const shipmentData = {
      order_id: orderDetails.orderId,
      pickup_city: orderDetails.pickupCity,
      delivery_city: orderDetails.deliveryCity,
      weight: orderDetails.weight,
      product_name: orderDetails.productName,
      shipping_address: orderDetails.shippingAddress,
      customer_phone: orderDetails.customerPhone,
      customer_email: orderDetails.customerEmail,
      shipping_method: "shiprocket_method" // Customize based on the method
    };

    // Create shipment
    const shipmentResponse = await axios.post(
      `${API_URL}courier/shipments/create`,
      shipmentData,
      {
        headers: { Authorization: `Bearer ${token}` }
      }
    );

    // Handle the response (contains tracking info, label, etc.)
    const shipmentInfo = shipmentResponse.data;
    console.log("Shipment Created:", shipmentInfo);
    return shipmentInfo;

  } catch (error) {
    console.error("Error creating shipment:", error);
    return { error: "Error creating shipment. Please try again." };
  }
}

module.exports = { createShipment };
